import pp from '../../images/Artwork/pp.png';

const FeedbackList = (props) =>(
    <article className="feedBack-list mb-2 rounded ">
        <p className="mb-0 p-2">
            <img src={pp} alt="profile" width="80" className="pl-3 pr-3 img-fluid"/>
            The Best Part of Waking Up is Customers in Your Cup.</p>
    </article>
)
export default function Feedback(){
    return(
        <section className="container pt-5 pb-5">
            <header className="text-center mb-5">
                <h2 className="text-darkblue">Our Customers Feedback</h2>
            </header>
            <main className="row m-0">
                <article className="col-md-5">
                    <h4 className="text-darkblue">The Customers Sign Means</h4>
                    <h3 className="text-lightorange">Happy Motoring.</h3>
                </article>
                <aside className="col-md-7">
                        <FeedbackList />
                        <FeedbackList />
                        <FeedbackList />
                </aside>
            </main>
        </section>
    )
}