import React from 'react';
import {Link} from 'react-router-dom';
import CardPackage from './backage.component';

import close from '../../images/icons/close.png';
import './long-term.styles.css';

export default function ChoosePackage(props){
    return(
    <main className="bg-R pt-4 pb-4 container">
        <article className="row  justify-content-end align-items-center m-0">
            <h3 className="text-darkblue col-md-6 text-right">{props.title}</h3>
            <figure className="col-3 text-right p-0 mb-0">
                  <Link to='/'>  <img width="20" src={close} alt="close" className="img-flui" /> </Link>
            </figure>
        </article>
        <section className="container">
            <div style={{minHeight : '33rem'}} className="row justify-content-end align-items-center m-0">
                <div className="col-lg-3 p-0">
                    <CardPackage title="Standard"/>
                </div>
                <div className="col-lg-3 p-0">
                    <CardPackage title="Premium"/>
                </div>
                <div className="col-lg-3 p-0">
                    <CardPackage title="Luxury"/>
                </div>
            </div>
        </section>
    </main>
    )
}