import slide from '../../images/Artwork/limousine3.png';
import plane from '../../images/icons/plane.png';
import clock from '../../images/icons/clock.png';
import calendar from '../../images/icons/calendar.png';
import car from '../../images/icons/car.png';

const LimousineTypes =(props)=>(
    <article className="col-md-6 text-center mb-5 p-relatve" >
        <div className="p-absolute" style={{top : '-33%' , right : '10%'}}>
                <img src={props.icon} alt={props.icon}/>
        </div>
        <div className="border pt-5 pb-5">
            <p className="h6 text-darkblue">{props.text}</p>
        </div>
    </article>
)
export default function Limousine(){
    return(
        <section className="pt-5">
            <header className="text-center  row m-0 justify-content-center align-items-center">
                <h3 className="text-darkblue col-md-12">Limousine Car Rental</h3>
                <h6  className="text-darkblue col-md-6 mb-4">ECRAL offers safe, professional and comfortable limousines transportation solutions for corporate and private customers.</h6>
            </header>
            <main className="row m-0 align-items-center">
                <figure className="col-md-7 p-0 mb-5">
                    <img src={slide} alt="slide" className="img-fluid"/>
                </figure>
                <article className="col-md-5 row">
                    <LimousineTypes icon={plane} text="Airport transfer"/>
                    <LimousineTypes icon={clock} text="Hourly Limousine"/>
                    <LimousineTypes icon={calendar} text="Full Day Limousinse"/>
                    <LimousineTypes icon={car} text="One way & Round"/>
                </article>
            </main>
        </section>
    )
}