import React from 'react';
import {Link} from 'react-router-dom';

import {Navbar, Nav } from 'react-bootstrap';
import Logo from '../../images/logos/ECRAL-Logo.png';
import arrow from '../../images/icons/down-arrow.png';
import menu from '../../images/icons/menu.png';
import world from '../../images/icons/world.png';


export default function Header() {
    return (
        <>
            <Navbar className="container" bg="transparent" expand="lg">
                <nav className="row m-0">
                    <Nav.Link  style={{fontSize :'14px'}} className="text-darkblue font-weight-bold p-1" href="#home">Corporate Rental</Nav.Link>
                    <Nav.Link  style={{fontSize :'14px'}} className=" text-darkblue font-weight-bold p-1"  href="#link">limousine</Nav.Link>
                </nav>
                <nav className="ml-auto row m-0">
                        <Nav.Link style={{fontSize :'13px'}} className=" text-darkblue text-uppercase p-1" href="#home">
                            Egy  
                            <img src={arrow} className="img-fluid p-1" width="18"  alt="arrow"/>
                            <span className="text-darkblue pl-2">|</span>
                        </Nav.Link>
                        <Nav.Link style={{fontSize :'13px'}} className="text-darkblue p-1" href="#link">
                            <img src={world} className="img-fluid p-1" width="25" alt="world"/>
                            English
                        </Nav.Link>
                    </nav>
            </Navbar>
            <Navbar className="container" bg="transparent" expand="lg">
                <Navbar.Brand className="">
                    <img src={Logo} className="w-75" alt="logo"/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ml-auto">
                        <Nav.Link className="text-darkblue font-weight-bold" href="#home">
                            <img src={menu} className="img-fluid p-1" width="25" alt="menu"/>
                            Menu   <span className="text-darkblue pl-2 font-weight-normal">|</span></Nav.Link>
                        <Nav.Link className="text-darkblue font-weight-bold  mr-2" href="#link">Sign in</Nav.Link>
                        <Nav.Link className="btn-orange m-1 mr-2" href="#link">Rent a car</Nav.Link>
                        <Link className="btn-transparent m-1" to='/choosePackage'>Long Term Rent</Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </>
    )
}