import { Input, InputLabel, InputAdornment, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from '@material-ui/core';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import SmartphoneIcon from '@material-ui/icons/Smartphone';

export default function Form() {
    return (
        <form className="form-long-term text-center">
            <FormControl className="mb-3" fullWidth>
                <InputLabel
                    htmlFor="name">
                    <h5 className="text-weight-bold text-darkblue mb-3">Full Name *</h5>
                </InputLabel>
                <Input
                    id="name"
                    placeholder="Enter your Full name"
                    startAdornment={
                        <InputAdornment position="start">
                            <PermIdentityIcon className="text-lightblue" />
                        </InputAdornment>
                    }
                />
            </FormControl>
            <FormControl className="mb-3" fullWidth>
                <InputLabel
                    htmlFor="email">
                    <h5 className="text-weight-bold text-darkblue">Email *</h5>
                </InputLabel>
                <Input
                    id="email"
                    placeholder="Enter your e-mail"
                    type="email"
                    startAdornment={
                        <InputAdornment position="start">
                            <MailOutlineIcon className="text-lightblue" />
                        </InputAdornment>
                    }
                />
            </FormControl>
            <FormControl className="mb-3" fullWidth>
                <InputLabel
                    type="tel"
                    className="h5 text-weight-bold"
                    htmlFor="phone">
                    <h5 className="text-weight-bold text-darkblue">Phone *</h5>
                </InputLabel>
                <Input
                    id="phone"
                    placeholder="Enter your mobile"
                    startAdornment={
                        <InputAdornment position="start">
                            <SmartphoneIcon className="text-lightblue" />
                        </InputAdornment>
                    }
                />
            </FormControl>
            <FormControl className="mb-3 text-left" fullWidth component="fieldset">
                <FormLabel component="legend">
                    <h5 className="text-darkblue mb-0">Rent Period </h5>
                </FormLabel>
                <RadioGroup row aria-label="position" name="position" defaultValue="end" className="row m-0">
                    <FormControlLabel
                        className="custom-radio pr-3"
                        value="three"
                        control={<Radio className="text-darkblue" />}
                        label="3-5 Months"
                        labelPlacement="end"
                    />
                    <FormControlLabel
                        className="custom-radio pr-3"
                        value="six"
                        control={<Radio className="text-darkblue" />}
                        label="6-11 Months"
                        labelPlacement="end"
                    />
                    <FormControlLabel
                        className="custom-radio pr-3"
                        value="twilve"
                        control={<Radio className="text-darkblue" />}
                        label="12+ Months"
                        labelPlacement="end"
                    />
                </RadioGroup>
            </FormControl>
            <FormControl className="">
                <button className="btn-orange ">Send Request</button>
            </FormControl>
        </form>
    )
}