import { Route, Switch, BrowserRouter  , HashRouter} from 'react-router-dom';

import Home from './components/home/index.component';
import ChoosePackage from './components/Long-term-rent/index.component';
import LongTermForm from './components/Long-term-rent/long-term-form.component';

import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import './App.css';



function App() {
  const theme = createMuiTheme({
    palette: {
      primary: {
        main: '#185B71'
      },
      secondary: {
        main: '#185B71'
      }
    },
  });
  return (
    <ThemeProvider theme={theme}>
      <HashRouter>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/choosePackage">
          <ChoosePackage />
        </Route>
        <Route exact path="/longTerm">
          <LongTermForm />
        </Route>
      </Switch>
    </HashRouter>
    </ThemeProvider>
  );
}

export default App;
