import React from 'react';
import CardImage from './card-image.component';
import car1 from '../../images/cars/car1.png';
import car2 from '../../images/cars/car2.png';
import car3 from '../../images/cars/car3.png';
import car4 from '../../images/cars/car4.png';
import car5 from '../../images/cars/car5.png';



export default function CarOffer(){
    return(
        <>
            <section className="container mt-5 mb-5">
                <main className="row mt-5">
                    <article className="col-md-4 ">
                        <h2 className="mb-5 text-darkblue">Top Car Rental Offers</h2>
                        <p className="d-inline mb-0 text-darkblue font-weight-bold">Don't Miss Our</p>
                        <h2 className="mb-0 text-lightorange">Special Offers </h2>
                        <p className="mr-1 mb-0 d-inline-block text-darkblue font-weight-bold">For</p><h3 className="text-darkblue d-inline-block"> Limited Time</h3> 
                        <h2 className="d-block text-lightorange font-weight-bolder h1">HURRY UP</h2>
                    </article>
                    <aside className="col-md-8 row m-0 row-cols-3 justify-content-end text-align-end mt-5">
                        <div className="col"></div>
                        <CardImage carImage={car1}/>
                        <CardImage carImage={car2}/>
                        <CardImage carImage={car3}/>
                        <CardImage carImage={car4}/>
                        <CardImage carImage={car5}/>

                    </aside>
                </main>
            </section>
        </>
    )
}