import slide from '../../images/Artwork/rent-car.png';
import check from '../../images/icons/check.png';

const Step = (props) => (
    <div className="text-center p-0 col-md-2 col-3">
        <button 
            className="custom-shadow step-box border-0 pr-3 pl-3 pt-2 pb-2  text-white h4" 
        >{props.number}</button>
        <small style={{fontSize : '13px'}} className="text-white text-weight-bold d-block">{props.text}</small>
    </div>
)
const ListItem = (props) => (
    <li className="text-white font-weight-bold mb-3" style={{fontSize : '18px' , listStyle : 'none'}}>
        <img src={check} width="45" className="pr-3" alt="check icon"/>{props.text}
    </li>
)
export default function RentACar(){
    return(
        <section className=" bg-darkblue ">
            <section className="container  pb-2">
                <main className="row m-0 p-relative">
                    <article className="col-md-7 p-0 pt-5">
                        <haeder className="text-white">
                            <h3 className=" mb-5 ml-3" >Start Rent a Car Now!</h3>
                        </haeder>
                        <section className="row m-0 " style={{minHeight : '7rem'}}>
                            <Step number="1" text="Sign Up"/>
                            <Step number="2" text="Choose a Car"/>
                            <Step number="3" text="Extras & Protection"/>
                            <Step number="4" text="Payment"/>
                        </section>
                        <ul className="ml-3 mt-3 mb-5 pl-0">
                            <ListItem text="Booking Online"/>
                            <ListItem text="Enjoy Special Disccounts"/>
                            <ListItem text="Track Your Requests"/>
                            <ListItem text="24/7 Customer Support"/>
                        </ul>
                    </article>
                    <figure className="col-md-6 d-web p-absolute" style={{top : '0' , right : '5px'}}>
                        <img src={slide} alt="slide" style={{maxWidth : '34rem'}}/>
                    </figure>
                </main>
        </section>
        </section>
    )
}