import React from 'react';
import { Link } from 'react-router-dom';
import Form from './form.component';
// styles icons and images
import close from '../../images/icons/close.png';
import arrow from '../../images/icons/left-arrow.png';
import slide from '../../images/Artwork/long-term-form.png';

export default function LongTermForm() {
    return (
        <main className="container pt-3 pb-3">
            <section className="row ">

                <figure className="col-12 text-right p-2">
                    <Link to="/"><img width="20" src={close} alt="close" className="img-fluid" /></Link>
                </figure>
            </section>
            <section className="row align-items-center">
                <article className="col-12">
                    <Link to="/choosePackage">
                        <h4 className="text-darkblue  d-inline">
                            <img src={arrow} alt="left arrow" className="img-fluid mr-2" />
                        Long Term Rent</h4>
                    </Link>
                </article>
                <aside className="col-md-6  order-sm-1 order-2">
                    <Form />
                </aside>
                <figure className="col-md-6 mt-3 text-center  order-sm-2 order-1">
                    <img width="500" src={slide} alt="slide" className="img-fluid" />
                </figure>
            </section>
        </main>
    )
}