import React from 'react';
import {Col} from 'react-bootstrap';

export default function CardImage(props){
    return(
        <Col md={4} xs={6}>
            <figure className="p-relative">
                <figureCaption className="overlay">
                    <span className="bg-darkblue text-lightorange p-2 p-absolute" style={{top : '0',right : '0'}}>
                        save <br/> 20%
                    </span>
                    <span className="text-white p-absolute p-2" style={{bottom : '0',left : '0'}}>
                        <p className="mb-0 font-weight-bold">3000 L.E <br/> <del>4500 L.E </del></p>
                    </span>
                </figureCaption>
                <img className="w-100" style={{maxHeight : '11rem'}} src={props.carImage} alt="car offer"/>
            </figure>
        </Col>  
    )
}