import React from 'react';
import {Link} from 'react-router-dom';
import car from '../../images/cars/hyundai-tucsons.png';
import CheckIcon from '@material-ui/icons/Check';

export default function CardPackage(props){
    const ListItem = (props) => (
        <article className="row">
            <p 
                className="col-10 text-left text-capitalize mb-0" 
                style={{fontWeight : props.check? "bold" : "normal" , fontSize : '14px'}}>
                    {props.title}
            </p>
            { props.check &&
                <figure className="col-2 text-right  mb-1" style={{fontSize : "bold"}}>
                    <CheckIcon style={{width : '16px' , heght : '16px'}}/>
                </figure>
            }
        </article>
    )
    return(
        <section className="card-backage p-3 border">
                <h4 className="text-center w-100 text-capitalize">{props.title}</h4>
                <figure className="text-center w-100">
                    <img src={car} alt="car"/>
                </figure>
                <article className="w-100">
                    <p className="mb-0 text-center text-capitalize">starting from </p>
                    <p className="w-100 text-center"><span className="h4">20000 / </span>Month</p>
                    <ListItem title="Lorem ipsum" check="true"/>
                    <ListItem title="Lorem ipsum Dolor sit amet" check="true" />
                    <ListItem title="Consectetur adipisicin" check="true"/>
                    <ListItem title="Consectetur adipisicin" check="true" />
                    <ListItem title="Consectetur adipisicin" />

                    <ListItem title="Sed do eiusmo"/>
                </article>
                <form className="w-100 text-center mt-2 btn-only-on-hover w-100">
                    <Link to="/longTerm" className="btn-orange d-block">Choose</Link>
                </form>
        </section>
    )
}