import React from 'react';

import { Input, InputLabel, InputAdornment, FormControl, FormLabel, RadioGroup, FormControlLabel, Checkbox } from '@material-ui/core';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import QueryBuilderIcon from '@material-ui/icons/QueryBuilder';

export default function Filters() {
    return (
        <section className="container mb-3">
            <form className="bg-white pt-3 pb-3 row m-0 align-items-end justify-content-center">
                <div className="col-md-4 mb-2">
                    <InputLabel
                        style={{fontSize : '14px'}}
                        className="d-inline-block text-lightblue mr-3"
                        htmlFor="date">Pick up location
                    </InputLabel>
                    <FormControlLabel
                        className="d-inline-block custom-checkbox pt-2"
                        value="three"
                        control={<Checkbox className="text-darkblue p-1" />}
                        label="Different Drop Off Location"
                        labelPlacement="end"
                    />
                    <Input
                        fullWidth
                        id="date"
                        type="location"
                        aria-label="kkk"
                        endAdornment={
                            <InputAdornment position="end">
                                <LocationOnIcon className="" />
                            </InputAdornment>
                        }
                    />
                </div>
                <div className="col-md-6 row m-0 p-0">
                    <div className="col-md-3 mb-2">
                        <InputLabel
                            className="text-lightblue"
                            style={{fontSize : '14px'}}
                            htmlFor="date">Pick Up Date
                        </InputLabel>
                        <Input
                            id="date"
                            type="date"
                            className="text-darkblue"
                            fullWidth
                        />
                    </div>
                    <div className="col-md-3 mb-2">
                        <InputLabel
                            className="text-lightblue"
                            style={{fontSize : '14px'}}
                            htmlFor="time">Time
                            </InputLabel>
                        <Input
                            fullWidth
                            id="time"
                            type="time"
                        />
                    </div>
                    <div className="col-md-3 mb-2">
                        <InputLabel
                            className="text-lightblue"
                            style={{fontSize : '14px'}}
                            htmlFor="date">Drop Off Date
                            </InputLabel>
                        <Input
                            fullWidth
                            id="date"
                            type="date"
                        />
                    </div>
                    <div className="col-md-3 mb-2">
                        <InputLabel
                            className="text-lightblue"
                            style={{fontSize : '14px'}}
                            htmlFor="time">Time
                            </InputLabel>
                        <Input
                            fullWidth
                            id="time"
                            type="time"
                        />
                    </div>
                </div>
                <div className="col-md-2 text-center mb-2 mt-2">
                    <button className="btn-darkblue ">Find Cars</button>
                </div>
            </form>
        </section>
    )
}